/* @import url('https://fonts.googleapis.com/css?family=Nunito:300,400,600,700,800&subset=latin-ext');


========================================================================
   Component: Container
 ========================================================================== */
/*
 * 1. Box sizing has to be `content-box` so the max-width is always the same and
 *    unaffected by the padding on different breakpoints. It's important for the size modifiers.
 */
.container {
  /* 1 */
  box-sizing: content-box;
  max-width: 1140px;
  margin-left: auto;
  margin-right: auto;
}

/*
   * Micro clearfix
   */
.container::before,
.container::after {
  content: "";
  display: table;
}

.container::after {
  clear: both;
}

/*
   * Remove margin from the last-child
   */
.container> :last-child {
  margin-bottom: 0;
}

/*
   * Remove padding from nested containers
   */
.container .container {
  padding-left: 0;
  padding-right: 0;
}

/* Size modifier
   ========================================================================== */
.container-xsmall {
  max-width: 750px;
  margin-left: auto;
  margin-right: auto;
}

.container-small {
  max-width: 900px;
  margin-left: auto;
  margin-right: auto;
}

.container-large {
  max-width: 1600px;
  margin-left: auto;
  margin-right: auto;
}

.container-expand {
  max-width: none;
  margin-left: auto;
  margin-right: auto;
}


/* Expand modifier
 ========================================================================== */
/*
* Expand one side only
*/
.container-expand-left {
  margin-left: 0;
}

.container-expand-right {
  margin-right: 0;
}

/* Phone landscape and bigger */
@media (min-width: 640px) {

  .container-expand-left.container-xsmall,
  .container-expand-right.container-xsmall {
    max-width: calc(50% + (750px / 2) - 30px);
  }

  .container-expand-left.container-small,
  .container-expand-right.container-small {
    max-width: calc(50% + (900px / 2) - 30px);
  }
}

/* Tablet landscape and bigger */
@media (min-width: 960px) {

  .container-expand-left,
  .container-expand-right {
    max-width: calc(50% + (1200px / 2) - 40px);
  }

  .container-expand-left.container-xsmall,
  .container-expand-right.container-xsmall {
    max-width: calc(50% + (750px / 2) - 40px);
  }

  .container-expand-left.container-small,
  .container-expand-right.container-small {
    max-width: calc(50% + (900px / 2) - 40px);
  }

  .container-expand-left.container-large,
  .container-expand-right.container-large {
    max-width: calc(50% + (1600px / 2) - 40px);
  }
}


/* ---------------------------------- */
/* section
------------------------------------- */

.section {
  box-sizing: border-box;
  position: relative;
  padding-top: 40px;
  padding-bottom: 40px;
}

/* Desktop and bigger */
@media (min-width: 960px) {
  .section {
    padding-top: 70px;
    padding-bottom: 70px;
  }
}

/*
 * Micro clearfix
 */
.section::before,
.section::after {
  content: "";
  display: table;
}

.section::after {
  clear: both;
}

/*
 * Remove margin from the last-child
 */
.section> :last-child {
  margin-bottom: 0;
}


/* Size modifiers
 ========================================================================== */

/*
* Small
*/
.section-small {
  padding-top: 15px;
  padding-bottom: 15px;
  position: relative;
}

/*
* Large
*/
.section-large {
  padding-top: 50px;
  padding-bottom: 50px;
  position: relative;
}

/*
* XLarge
*/
.section-xlarge {
  padding-top: 140px;
  padding-bottom: 140px;
}

/* Tablet landscape and bigger */
@media (min-width: 960px) {
  .section-small {
    padding-top: 25px;
    padding-bottom: 25px;
    position: relative;
  }

  .section-xlarge {
    padding-top: 210px;
    padding-bottom: 210px;
  }

  .section-large {
    padding-top: 140px;
    padding-bottom: 140px;
  }
}


