/* ---------------------------------- */
/* responsive tab
------------------------------------- */

.responsive-tab {
  position: relative;
  z-index: 1;
  clear: both;
  width: 100%;
  height: 52px;

  /* Force Hardware Acceleration */
  transform: translateZ(0);
  will-change: transform;
  transition: transform .5s;
}

.responsive-tab ul,
.responsive-tab li,
.responsive-tab a {
  height: 100%;
}

.responsive-tab ul {
  /* enables a flex context for all its direct children */
  display: flex;
  padding: 0;
  margin: 0;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.responsive-tab ul::after {
  clear: both;
  content: "";
  display: block;
}

.responsive-tab:before {
  position: absolute;
  bottom: 0;
  content: '';
  background: #e8e8e8;
  width: 100%;
  height: 1px;
  border-radius: 27px;
}

.responsive-tab li {
  display: inline-block;
  flex-shrink: 0;
  position: relative;
}

.responsive-tab a {
  display: block;
  color: #8e8e8e;
  opacity: .6;
  line-height: 50px;
  padding: 0px 13px;
  font-weight: 500;
  font-size: 13px;
  position: relative;
}

.responsive-tab a i {
  margin-right: 9px;
}

.responsive-tab li a:hover {
  color: #3e416d;
  opacity: .8;
}

.responsive-tab .uk-active:hover,
.responsive-tab .uk-active a {
  color: #1a73e8 !important;
  opacity: 1;
}

.responsive-tab .uk-active a:before {
  position: absolute;
  bottom: 0;
  content: '';
  width: 100%;
  height: 4px;
  left: 0;
  background: #1a73e8;
}

.uk-light .responsive-tab:before {
  background: transparent;
}

.uk-light .responsive-tab .uk-active a,
.uk-light .responsive-tab li a:hover {
  color: white !important;
}

.uk-light .responsive-tab .uk-active a:before {
  background-color: white
}

@media only screen and (min-width: 1024px) {

  .responsive-tab {
    height: 56px;
    overflow: visible;
  }

  /* reset mobile style */
  .responsive-tab ul {
    display: block;
  }

  /* reset mobile style */
  .responsive-tab li {
    float: none;
    flex-shrink: 1;
    flex: 1 0;
  }

  .responsive-tab a {
    font-size: 15px;
    margin-right: 10px;
    padding: 0 10px;
    line-height: 55px;
  }
}

/* ---------------------------------- */
/* style-1
------------------------------------- */

.style-1.responsive-tab:before,
.style-1.responsive-tab .uk-active a:before {
  background: transparent;
}

/* ---------------------------------- */
/* style-2
------------------------------------- */

.responsive-tab.style-2 .uk-active a:before {
  width: 100%;
  width: 20%;
  height: 4px;
  left: 50%;
  transform: translate(-50%, -50%);
}

.responsive-tab.style-2:before {
  background: transparent;
}

.responsive-tab.style-2 .uk-active,
.responsive-tab.style-2 .uk-active a {
  color: #0a0a0a;
}


/* ---------------------------------- */
/* style-3
------------------------------------- */
.responsive-tab.style-3 .uk-active a:before,
.responsive-tab.style-3:before {
  display: none;
}

@media only screen and (min-width: 1024px) {

  .responsive-tab.style-3 {
    height: auto;
  }

  .responsive-tab.style-3 li a {
    font-size: 18px;
    line-height: 44px;
  }

  .responsive-tab.style-3 li {
    display: block;
  }

  .responsive-tab.style-3 ul,
  .responsive-tab.style-3 li,
  .responsive-tab.style-3 a {
    height: 100%;
  }

  .responsive-tab.style-3 li .responsive-tab.style-3 a {
    height: 100%;
  }

}



