/* ---------------------------------- */
/* Back to top
------------------------------------- */
#backtotop {
  position: fixed;
  right: 0;
  opacity: 0;
  visibility: hidden;
  bottom: 25px;
  margin: 0 25px 0 0;
  z-index: 999;
  transition: 0.35s;
  transform: translateY(10px);
}

#backtotop.visible {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

#backtotop a {
  text-decoration: none;
  border: 0 none;
  display: block;
  width: 46px;
  height: 46px;
  background-color: #1a73e8;
  opacity: 1;
  transition: all 0.3s;
  border-radius: 100%;
  text-align: center;
  font-size: 26px;
  display: none;
}

body #backtotop a {
  color: #ffffff;
}

#backtotop a:after {
  content: "\e931";
  font-family: "Feather-Icons";
  position: relative;
  display: block;
  top: 50%;
  transform: translateY(-50%);
}

@media (max-width: 768px) {
  #backtotop {
    display: none;
  }
}


