  position: relative
}

.button.icon-label .inner-icon {
  line-height: 1;
  position: absolute;
  width: 3em;
  height: 100%;
  margin: 0;
  text-align: center;
  border-radius: 0;
  background-color: rgba(0, 0, 0, .1);
  display: flex;
  justify-content: center;
  align-items: center;
}

.button.icon-label .inner-icon:not(:first-child) {
  top: 0;
  right: 0;
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit
}

.button.icon-label .inner-icon:not(:last-child) {
  top: 0;
  left: 0;
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit
}

.button.icon-label .inner-text:not(:first-child) {
  padding-left: 2em;
}

.button.icon-label .inner-text:not(:last-child) {
  padding-right: 2em
}


.button.icon .inner-text:not(:first-child) {
  margin-left: .75em
}

.button.icon .inner-text:not(:last-child) {
  margin-right: .75em
}

/* Button animation  */

.button.animated-x,
.button.animated-y {
  position: relative;
  overflow: hidden !important
}

.button.animated-x .inner-visible {
  position: relative;
  right: 0;
  transition: right .3s ease 0s
}

.button.animated-x .inner-hidden {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
  right: -100%;
  transition: right .3s ease 0s
}

.button.animated-x:hover .inner-hidden {
  right: 0
}

.button.animated-x:hover .inner-visible {
  right: 150%
}

.button.animated-y .inner-visible {
  position: relative;
  top: 0;
  transition: top .3s ease 0s
}

.button.animated-y .inner-hidden {
  position: absolute;
  top: 50%;
  width: 100%;
  transform: translateY(-50%);
  bottom: -190%;
  left: 0;
  transition: bottom .3s ease 0s;
}

.button.animated-y:hover .inner-hidden {
  bottom: -16px;
}

.button.animated-y:hover .inner-visible {
  top: 100px
}

.button.icon-only {
  display: flex;
  justify-content: center;
  align-items: center;
}

/* button groupss  */
.buttons {
  display: inline-flex;
  flex-direction: row;
  vertical-align: baseline;
}

.buttons .button {
  border-radius: 0;
  width: 10;
}

.buttons .button:first-child {
  border-left: none;
  margin-left: 0;
  border-top-left-radius: .28571429rem;
  border-bottom-left-radius: .28571429rem;
}

.buttons .button:last-child {
  border-top-right-radius: .28571429rem;
  border-bottom-right-radius: .28571429rem;
}

/* button conditions */
.buttons .or {
  position: relative;
  width: .3em;
  height: auto;
  z-index: 3;
}

.buttons .or:before {
  position: absolute;
  text-align: center;
  border-radius: 500rem;
  content: 'or';
  top: 50%;
  left: 50%;
  background-color: #fff;
  text-shadow: none;
  margin-top: -.89285714em;
  margin-left: -.89285714em;
  width: 1.78571429em;
  height: 1.78571429em;
  line-height: 1.78571429em;
  color: rgba(0, 0, 0, .4);
  font-style: normal;
  font-weight: 700;
  box-shadow: 0 0 0 1px transparent inset;
}

.buttons .or[data-text]:before {
  content: attr(data-text);
}

/* button groups icon */
.button-group-icon {
  display: inline-flex;
  flex-direction: row;
  vertical-align: baseline;
}

.button-group-icon .button {
  border-radius: 0
}

.button-group-icon .button:first-child {
  border-left: none;
  margin-left: 0;
  border-top-left-radius: .28571429rem;
  border-bottom-left-radius: .28571429rem;
}

.button-group-icon .button:last-child {
  border-top-right-radius: .28571429rem;
  border-bottom-right-radius: .28571429rem;
}


/* buttun Switch styles */

.btn-switch {
  display: inline-block;
  height: 18px;
  width: 37px;
  position: relative;
}

.btn-switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.btn-switch-slider {
  background-color: rgba(0, 0, 0, 0.09);
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  border-radius: 500px;
  bottom: 0;
  cursor: pointer;
  transition-property: background-color;
  transition-duration: .2s;
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.07);
  outline: none;
}

.btn-switch-slider:before {
  content: '';
  background-color: #a9a9a9;
  position: absolute;
  width: 21px;
  height: 21px;
  left: 0px;
  bottom: -1px;
  border-radius: 50%;
  transition-property: transform, box-shadow;
  transition-duration: .2s;
}


input:checked+.btn-switch-slider {
  background-color: #e8e8e8;
}

input:checked+.btn-switch-slider:before {
  transform: translateX(17px);
  background-color: #3e416d;
  box-shadow: 1px 1px 4px -1px #3e416d;
}





