/* ---------------------------------- */
/* Slider nav arrow  
------------------------------------- */


.btn-arrow-slider {
  height: 26px;
  cursor: pointer;
  display: inline-flex;
}

.btn-arrow-slides {
  margin: 0 5px;
  position: relative;
  padding: 17px 0;
}

.btn-arrow-slides span {
  display: block;
  position: relative;
  transition: width 0.3s ease 0s;
}

.btn-arrow-slides:before {
  content: '';
  display: block;
  width: 42px;
  height: 3px;
  border-radius: 30%;
  background-color: #1a73e8;
  position: relative;
  transition: width 0.3s ease 0s;
}

span.arrow-left::before,
span.arrow-left::after,
span.arrow-right::before,
span.arrow-right::after {
  content: "";
  display: block;
  width: 14px;
  height: 3px;
  background-color: #1a73e8;
  position: absolute;
  z-index: 1;
  top: -3px;
  border-radius: 7px;
  transition: all 0.3s ease 0s;
}


span.arrow-left::before,
span.arrow-left::after {
  left: 2px;
  transform: rotate(0deg);
  transform-origin: 0px 2px;
}

span.arrow-right::before,
span.arrow-right::after {
  right: 2px;
  transform: rotate(0deg);
  transform-origin: 14px 2px;
}



span.arrow-left::before {
  transform: rotate(-42deg);
}

span.arrow-left::after {
  transform: rotate(42deg);
}

span.arrow-right::before {
  transform: rotate(42deg);
}

span.arrow-right::after {
  transform: rotate(-42deg);
}



.btn-arrow-slides.uk-invisible span {
  visibility: visible !important;
}

.btn-arrow-slides.uk-invisible span:before,
.btn-arrow-slides.uk-invisible span:after {
  background-color: #e0e0e0;
  transform: rotate(0deg);
  margin-top: 0px;
  opacity: 0.1;
}

.btn-arrow-slides.uk-invisible:before {
  content: '';
  display: block;
  width: 24px !important;
  visibility: visible !important;
  background-color: #bdbdbd;
  opacity: .4;
}






.btn-arrow-slides.left:before {
  width: 24px !important;
  background-color: #bdbdbd;
}

.btn-arrow-slides.left:hover:before {
  width: 45px !important;
  background-color: #1a73e8;
}

.btn-arrow-slides.left span:before,
.btn-arrow-slides.left span:after {
  background-color: #bdbdbd;
  transform: rotate(0deg);
  margin-top: 0px;
}

.btn-arrow-slides.left:hover span.arrow-left::before {
  transform: rotate(-42deg) !important;
  background-color: #1a73e8;
}

.btn-arrow-slides.left:hover span.arrow-left::after {
  transform: rotate(42deg) !important;
  background-color: #1a73e8;
}


@media (max-width: 960px) {
  .btn-arrow-slides:before {
    content: '';
    display: block;
    width: 30px;
    height: 3px;
  }

  span.arrow-left::before,
  span.arrow-left::after,
  span.arrow-right::before,
  span.arrow-right::after {
    content: "";
    display: block;
    width: 10px;
    height: 3px;
  }

  span.arrow-right::before,
  span.arrow-right::after {
    transform-origin: 11px 2px;
  }

  .btn-arrow-slides.uk-invisible:before {
    width: 19px !important;
  }
}




