/* ---------------------------------- */
/* Dashboard chats
------------------------------------- */
.chats-container {
  margin: 0;
  /* background-color: #fff; */
  border: 1px solid #f1f3f4;
  border-top: 0;
}


/* Scrollbar Adjustments */
.chats-inbox ul {
  max-height: 875px;
}

.message-content-inner {
  max-height: 750px;
}

.message-content,
.chats-container-inner .chats-inbox {
  overflow: hidden;
}

.message-content-inner {
  overflow-y: scroll;
  width: calc(100% - 24px);
}

.chats-container-inner .chats-inbox ul {
  overflow-y: scroll;
  width: calc(100% + 17px);
}

.message-content-inner::-webkit-scrollbar,
.chats-container-inner .chats-inbox ul::-webkit-scrollbar {
  width: 17px;
}

/*  Scrollbar Hide - End */

.chats-headline {
  height: 42px;
  font-size: 16px;
  margin: 0;
  padding: 18px 24px;
  color: #333;
  /* background-color: #fff; */
  display: block;
  border-bottom: 1px solid #f1f3f4;
  border-radius: 4px 4px 0 0;
  position: relative;
}

.chats-headline input {
  background-color: #f1f3f4;
  border: 1px solid #f1f3f4;
  box-shadow: none;
  height: 38px;
  padding: 14px;
  font-size: 14px;
  margin: 0;
  border-radius: 8px;
}

.chats-headline input:focus {
  box-shadow: none;
}

.chats-headline .input-with-icon i {
  font-size: 18px;
  right: 14px;
}

.chats-headline h4 {
  font-size: 16px;
  color: #333;
  padding: 6px 0;
  padding-bottom: 0;
  margin: 0;
  font-weight: 600;
  display: inline-block;
}

.chats-headline h4 span {
  display: block;
  font-size: 10px;
}

.message-action {
  border-radius: 50px;
  font-size: 13px;
  color: #666;
  font-weight: 500;
  display: inline-block;
  transition: 0.3s;
  color: #666;
  padding: 0;
  position: absolute;
  top: 50%;
  right: 30px;
  transform: translateY(-50%);
}

.message-action i {
  position: relative;
  top: 1px;
  margin-right: 1px;
  font-size: 25px;
  margin-left: 13px;
}

.message-action:hover {
  color: #ee3535;
}

/* List */
.chats-inbox ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.message-avatar img {
  display: inline-block;
  width: 54px;
  height: 54px;
  border-radius: 50%;
}

.chats-inbox ul li a {
  position: relative;
  display: block;
  padding: 30px;
}

.chats-inbox ul li {
  transition: 0.2s;
  position: relative;
  margin: 0 !important;
  padding: 0 !important;
}

.chats-inbox ul li:nth-child(2n) {
  background-color: #fafafa;
}

.chats-inbox ul li:last-child {
  border-bottom: none;
}

.chats-inbox ul li.active-message {
  background-color: #fafafa;
}

.chats-inbox ul li:before {
  position: absolute;
  top: 0;
  left: -4px;
  width: .25rem;
  height: 100%;
  background-color: #1b74e8;
  display: block;
  content: "";
  opacity: 0;
  transition: 0.3s;
}

.chats-inbox ul li:hover:before,
.chats-inbox ul li.active-message:before {
  opacity: 1;
  left: 0px;
}

.chats-inbox .message-avatar {
  position: absolute;
  left: 30px;
  top: 50%;
  transform: translateY(-50%);
}

.status-icon.status-online,
.notification-avatar.status-online:after,
.status-online:after {
  background-color: #38b653;
}

.status-icon,
.notification-avatar:after,
.user-avatar:after {
  position: absolute;
  content: "";
  height: 10px;
  width: 10px;
  background-color: #c0c0c0;
  bottom: 0;
  right: 0;
  display: block;
  border: 2.5px solid #fff;
  border-radius: 50%;
}

.chats-inbox .message-by {
  margin-left: 85px;
  font-size: 15px;
  color: #666;
}

.chats-inbox .message-by p {
  margin: 0;
  padding: 0;
  line-height: 25px;
  font-size: 15px;
}

.message-by-headline h5,
.message-by-headline span {
  display: inline-block;
}

.message-by-headline span {
  float: right;
  color: #969595;
  font-size: 14px;
}

.message-by-headline h5 {
  font-weight: 600;
  color: #888;
  margin: 0 0 7px 0;
  padding: 0;
}

.message-by-headline:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.message-by h5 i {
  background-color: #66676b;
  border-radius: 4px;
  line-height: 20px;
  font-size: 12px;
  color: #fff;
  font-style: normal;
  padding: 3px 8px;
  margin-left: 3px;
}


/* Single Message */
.chats-container-inner {
  display: flex;
}

.chats-container-inner .chats-inbox,
.chats-container-inner .message-content {
  flex: 1 1;
}

.chats-container-inner .message-content-inner {
  padding: 26px;
  padding-bottom: 0;
  flex-grow: 1;
}

.chats-container-inner .message-content {
  display: flex;
  flex-direction: column;
}

.chats-container-inner .chats-inbox {
  max-width: 300px;
  border-right: 1px solid #f1f3f4;
}

.chats-container-inner .chats-inbox ul li a {
  padding: 17px 20px;
}

.chats-container-inner .chats-inbox .message-by p {
  height: 26px;
  max-width: 205px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  font-size: 14px;
  color: #888;
  margin-right: 11px;
}

.chats-container-inner .message-avatar img {
  width: 40px;
  height: 40px;
}

.chats-container-inner .chats-inbox .message-avatar {
  left: 25px;
}

.chats-container-inner .chats-inbox .message-by {
  margin-left: 60px;
}

.chats-container-inner .chats-inbox .message-readed {
  float: right;
  margin-top: -21px;
  color: #0ae489;
}

.chats-container-inner .message-by-headline h5 {
  font-size: 14px;
  margin-bottom: 2px;
  max-width: 160px;
  line-height: 23px;
}

.chats-container-inner .message-by-headline span {
  font-size: 12.5px;
  position: absolute;
  top: 20px;
  right: 25px;
}

.chats-container-inner .active-message .message-by-headline h5,
.chats-container-inner .active-message .message-by p {
  color: black;
}

/* Message Bubbles */
.message-bubble {
  display: block;
  position: relative;
  margin-bottom: 20px;
}

.message-bubble .message-avatar {
  position: absolute;
  left: 0;
  top: 0;
}

.message-bubble .message-avatar img {
  width: 50px;
  height: 50px;
}

.message-bubble .message-text {
  margin-left: 62px;
  background-color: #f4f4f4;
  border-radius: 4px;
  padding: 10px 16px;
  position: relative;
  /* display: inline-block; */
  float: left;
  line-height: 23px;
  max-width: 70%;
  align-items: center;
}

.message-bubble.me .message-text {
  float: right;
}

.message-bubble .message-text p {
  font-size: 15px;
  padding: 0;
  margin: 0;
  line-height: 25px;
}


/* Message Bubble "me" */
.message-bubble.me .message-avatar {
  left: auto;
  right: 0;
}

.message-bubble.me .message-text {
  margin-left: 0;
  margin-right: 62px;
  background-color: #1b74e8;
  color: #fff;
  position: relative;
}

/* Arrow */
.message-bubble .message-text:before {
  content: "";
  width: 0;
  height: 0;
  border-top: 0px solid transparent;
  border-bottom: 11px solid transparent;
  border-right: 10px solid #f4f4f4;
  left: -8px;
  right: auto;
  top: 0px;
  position: absolute;
}

.message-bubble.me .message-text:before {
  border-top: 0px solid transparent;
  border-bottom: 11px solid transparent;
  border-left: 9px solid #1b74e8;
  border-right: none;
  right: -7px;
  left: auto;
}

/* Message Time Sign */
.message-time-sign {
  text-align: center;
  display: block;
  position: relative;
  margin-bottom: 25px;
}


.message-time-sign span {
  /* background-color: #fff; */
  font-size: 14px;
  color: #888;
  z-index: 11;
  position: relative;
  padding: 0 10px;
}

/* Reply */
.message-reply {
  border-top: 1px solid #f1f3f4;
  padding: 30px 16px;
  display: flex;
  align-items: flex-start;
  margin-top: 15px;
}

.message-reply textarea {
  height: 48px;
  min-height: 48px;
  padding: 10px 0;
  margin: 0 15px 0 0;
  flex: 1 1;
  font-weight: 400;
  line-height: 26px;
  box-shadow: none;
  border: none;
  box-sizing: border-box;
  resize: none;
}

.message-reply .button {
  border-radius: 50px !important;
  align-self: center;
  display: flex;
  width: 40px;
  height: 40px;
  position: relative;
  padding: 0;
  text-align: center;
  align-items: center;
  justify-content: center;
}

.message-reply .button i {
  padding: 0;
  margin: 0;
  position: absolute;
}


/* Typing Indicastor */
.typing-indicator {
  width: auto;
  display: inline-block;
  margin-left: 10px;
}

.typing-indicator span {
  height: 5px;
  width: 5px;
  float: left;
  margin: 0 1.5px;
  background-color: #808080;
  display: block;
  border-radius: 100%;
  opacity: 0.4;
}

.typing-indicator span:nth-of-type(1) {
  -webkit-animation: 1s blink infinite 0.33333s;
          animation: 1s blink infinite 0.33333s;
}

.typing-indicator span:nth-of-type(2) {
  -webkit-animation: 1s blink infinite 0.66666s;
          animation: 1s blink infinite 0.66666s;
}

.typing-indicator span:nth-of-type(3) {
  -webkit-animation: 1s blink infinite 0.99999s;
          animation: 1s blink infinite 0.99999s;
}

@-webkit-keyframes blink {
  50% {
    opacity: 1;
  }
}

@keyframes blink {
  50% {
    opacity: 1;
  }
}

/* chats Media Queries */
@media (max-width: 992px) {
  .chats-container-inner .chats-inbox {
    max-width: 300px;
  }
}

@media (max-width: 768px) {
  .chats-container-inner {
    display: block;
  }

  .chats-container-inner .chats-inbox {
    max-width: 100%;
    border-bottom: 1px solid #e4e4e4;
    border-right: none;

  }

  .chats-inbox ul {
    max-height: 500px;
  }

  .chats-container-inner .message-by-headline span {
    position: relative;
    top: 0;
    right: 0;
    display: block;
    float: none;
    padding: 0;
    margin: -4px 0 0 0;
  }

  .message-action {
    display: block;
    position: relative;
    right: 0;
    top: 0;
    transform: none;
    margin-top: -5px;
  }

  .chats-headline {
    max-height: 120px;
  }
}



