/* ---------------------------------- */
/* responsive tab
------------------------------------- */

.responsive-tab {
  position: relative;
  z-index: 1;
  clear: both;
  width: 100%;
  height: 52px;

  /* Force Hardware Acceleration */
  transform: translateZ(0);
  will-change: transform;
  transition: transform .5s;
}

.responsive-tab ul,
.responsive-tab li,
.responsive-tab a {
  height: 100%;
}

.responsive-tab ul {
  /* enables a flex context for all its direct children */
  display: flex;
  padding: 0;
  margin: 0;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.responsive-tab ul::after {
  clear: both;
  content: "";
  display: block;
}

.responsive-tab li {
  display: inline-block;
  flex-shrink: 0;
  position: relative;
}

.responsive-tab a {
  display: block;
  color: #0c0c0c;
  opacity: .6;
  line-height: 50px;
  padding-right: 17px;
  padding-left: 0;
  font-weight: 600;
  font-size: 15px;
  position: relative;
  text-transform: capitalize;
}

.responsive-tab a span {
  padding: 5px 9px;
  background: #e8f0fe;
  border-radius: 30px;
  font-size: 12px;
  margin-left: 3px;
}

.responsive-tab a i {
  /* font-weight: 700; */
  margin-right: 9px;
}

.responsive-tab li a:hover {
  color: #3e416d;
  opacity: .8;
}

.responsive-tab .uk-active,
.responsive-tab .uk-active a {
  color: #3e416d;
  opacity: 1;
}

.uk-light .responsive-tab:before {
  background: transparent;
}

.uk-light .responsive-tab .uk-active a,
.uk-light .responsive-tab li a:hover {
  color: white
}

.uk-light .responsive-tab .uk-active a:before {
  background-color: white
}

@media only screen and (min-width: 1024px) {

  .responsive-tab {
    height: 56px;
    overflow: visible;
  }

  /* reset mobile style */
  .responsive-tab ul {
    display: block;
  }

  /* reset mobile style */
  .responsive-tab li {
    float: none;
    flex-shrink: 1;
    flex: 1 0;
  }

  .responsive-tab a {
    font-size: 15px;
    margin-right: 20px;
    padding: 0;
    line-height: 55px;
  }
}

