------------------------------------- */
.photo-card {
  box-sizing: border-box;
  border: 3px solid white;
  position: relative;
  overflow: hidden;
  height: 150px;
  background-size: cover;
  display: flex;
  align-items: flex-end;
  transition: all ease .3s;
}

.photo-card:hover .photo-card-content {
  margin-bottom: 0;
}

.photo-card:before {
  position: absolute;
  content: '';
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
