/* ---------------------------------- */
/* Star Rating
------------------------------------- */
.star-rating {
  display: inline-block;
  position: relative;
}

.star-rating .star {
  display: inline-block;
  margin: 0;
  padding: 0;
  margin-right: 0;
  position: relative;
  line-height: 1;
}

.star-rating .star.half:after,
.star-rating .star:before {
  font-family: "Material-Icons";
  content: "\e988";
  display: block;
  color: #febe42;
  font-size: 19px;
}

.star-rating .star.empty:before {
  color: #ddd;
}

.rating:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}

.star-rating .star.half:before {
  color: #ddd;
}

.star-rating .star.half:after {
  color: #febe42;
  position: absolute;
  top: 0;
  width: 50%;
  display: block;
  height: 100%;
  overflow: hidden;
}

/* average rating before stars */
.star-rating .avg {
  float: left;
  border-radius: 4px;
  background-color: #febe42;
  color: #fff;
  font-size: 13px;
  line-height: 15px;
  font-weight: 700;
  padding: 3px 7px;
  position: relative;
  margin-right: 10px;
}

/* No stars */
.star-rating.no-stars .star {
  display: none;
}

.star-rating.no-stars:before {
  margin-right: 0;
}




