/* MESSAGES Box */ 

/* Hiding the scroll bar, but keeping scrollability.
    scrollbar still has some ghost margin to balance out. */
.messages {
    padding: 1rem 0 1rem 1.55rem;
    overflow: auto;
    flex: auto;
}

/* same */
div.messageViewer > div:first-child {
    -ms-overflow-style: none;
    scrollbar-width: none;
}
div.messageViewer > div:first-child::-webkit-scrollbar {
    display: none;
}
/* INFOBAR */ 
.infoBar {
    display: flex;
    border-radius: 4px 4px 0 0;
    height: 4.2rem;
    width: 100%;
    color: #545454;
  }
  
  .leftInnerContainer {
    display: flex;
    flex: 0.5 1;
    color: white;
  }
  
  .rightInnerContainer {
    display: flex;
    flex: 0.5 1;
    justify-content: flex-end;
    margin-right: 5%;
  }
/* INPUT */

/* if commented-out, its from the old layout,
not currently in use and on the chopping block! */

/* .chatForm {
    display: flex;
    border-top: 2px solid #aab8c2;
} */

.chatInput {
    border: none;
    border-radius: 1rem;
    font-size: 1.2em;
    overflow: hidden;
}

input:focus, textarea:focus, select:focus{
    outline: none;
}

.sendButton {
    color: #fff !important;
    font-size: 1.25rem;
    background: #2979FF;
    display: inline-block;
    border: none;
    border-radius: 0 0 0.25rem 0;
    width: 20%;
}
/* CHAT */ 

.outerChatContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    padding-bottom: 3.4rem;
}

.chatContainer {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border: solid 1px #e8e8e8;
    border-radius: 8px;
    height: 60%;
    width: 35%;
}
  
@media (min-width: 320px) and (max-width: 480px) {
    .outerChatContainer {
        height: 100vh;
    }

    .chatContainer {
        width: 100%;
        height: 100%;
    }
}
  
@media (min-width: 480px) {
    .chatContainer {
        width: 60%;
        height: 90vh;
    }
}
