/* SOCIAL CARD - PROFILE / POSTS*/ 

.textChanger {
    text-transform: none;
}

.card {
    box-shadow: 0 8px 12px 0 rgba(10, 35, 90, 0.2);
    transition: 0.4s;
    width: 90%;
    border-radius: 10px;
    background-color: whitesmoke !important;
}

/* .profContainer {
    width: 100vw;
    display: flex;
    flex-direction: column;
} */

.cardContainer {
    display: flex;
    flex-direction: column;
    margin: 1rem 10% 5rem 10%;
}
  
  
#social {
    flex-direction: row;
}

.socialBtn {
    transition: 0.3s;
    background-color: inherit;
    border: none;
}

.socialBtn i {
    color: #384047;
    font-size: 3rem;

}
.socialBtn:hover, .socialBtn:focus {
    letter-spacing: 0.3rem;
    box-shadow: 3px 5px 3px 0 rgba(0, 0, 0, 1);
    background-color: inherit;
}

.deleteBtn {
    font-size: 1rem !important;;
    background-color: #18a0fb !important;
    border: none;
    border-radius: 0.5rem;
}
.deleteBtn {
    display: block;
    font-size: 25px;
    color: #fff;
    background-color: inherit;
    border: none;
    transition: 0.3s;
}
  
.deleteBtn:hover, .deleteBtn:focus,
.sendSnip:hover, .sendSnip:focus {
    background-color: #18a0fb;
    box-shadow: 3px 5px 3px 0 rgba(0, 0, 0, 0.4);
}

.sendSnip {
    background-color: #18a0fb;
    max-width: 10rem;
    transition: 0.3s;
    border: none;
}
  

#date{
    float: right;
    padding-bottom: 1rem;
    font-family: inherit;
    font-size: 0.75rem;
    color: #545454;
}
.card {
    box-shadow: 0 8px 12px 0 rgba(10, 35, 90, 0.2);
    transition: 0.4s;
    width: 90%;
    border-radius: 10px;
    background-color: lightgray;
  }

  .getDown {
      margin-top: 1rem;
  }